<style scoped>

.telmplete-full {
    /*padding: 20px 20px;*/
}

.tab-full {
    padding-top: 10px;
}

.ivu-page {
    text-align: right;
    margin-right: 30px;
    padding: 10px 0px;
}

.fu {
    padding: 10px
}

.topBtnWrap {
    display: flex;
    justify-content: space-between;
}

.master-content-middle {
    display: -webkit-flex;
    /* Safari */
    display: flex;
    padding: 30px 10px;
    border: 1px solid #d7f0f2;
    align-items: center;
}

</style>

<template>

<div>
    <top-title title="权限功能设置"></top-title>
    <div class="fu">
        <div class="topBtnWrap">
            <div>
                <Button class="btnColor" @click="thisRouter" icon="plus" type="primary">新建权限功能</Button>
                <Button class="btnCancel" @click='deleteAll' :disabled="isDeletes" type="error">删除</Button>
                <Button type="text" v-on:click="trigger">筛选&nbsp;&nbsp;
                    <Icon type="arrow-down-b"></Icon>
                </Button>
            </div>
            <div>
                <Input v-model="value" placeholder="请输入权限功能名称" style="width: 300px"></Input>
                <Button class="btnColor" type="primary" icon="search" @click='search'>搜索</Button>
            </div>

        </div>
        <div class="master-content-middle" v-show="isShow">
            <div class="label">
                权限状态：
            </div>
            <div class="label-value">
                <Select v-model="roleStatus" style="width:200px" @on-change="getFilterData">
                    <Option :value="-99">全部</Option>
                    <Option :value="1">启用</Option>
                    <Option :value="0">删除</Option>
                </Select>
            </div>

        </div>
        <div>
            <div class="tab-full">
                <Table highlight-row @on-selection-change='selectData' :loading="loading" border ref="selection" :columns="columns" :data="data1"></Table>
            </div>
            <Page @on-change='clickPage' :total='pageSize' show-total></Page>
        </div>
    </div>
    <set-Role ref="setRole" @filterParent="roleList"></set-Role>
    <change-Permission ref="changePermission" @filterParent="roleList"></change-Permission>


    <Modal v-model="modalLogin" :styles="{top: '20px'}">
        <p>{{errorMsg}}</p>
        <div slot="footer">
            <Button type="error" size="large" @click="toLogin">重新登录</Button>
        </div>
    </Modal>

</div>

</template>

<script>

import PageSize from '../../../common/PageSize'
import topTitle from '@/components/common/topTitle'
import setRole from './child/setRole.vue'
import changePermission from './child/changePermission.vue'

export default {
    components: {
        topTitle, setRole, changePermission
    },
    data() {
        return {
            value: '',
            isShow: false,
            roleStatus: 1,
            loading: true,
            permissionArr: [],
            pageSize: "",
            pageNo: '',
            modal1: false,
            delBol: true,
            modalLogin: false,
            errorMsg: '',
            bolOk: null,
            paramsIndex: '',
            dataSysUserId: '',
            selectTabAllsysResourceid: '',
            deleAllArr: [],
            isAdds: true,
            isDeletes: true,
            deleteJurisdictionParams: [],
            data1: [],
            columns: [{
                    type: 'selection',
                    width: 60,
                    align: 'center'
                },

                {
                    title: '权限功能名称',
                    key: 'sysResourceName'
                }, {
                    title: '状态',
                    key: 'statusDesc'
                }, {
                    title: '层级路径',
                    key: 'parentUrl'
                }, {
                    title: '接口路径',
                    key: 'permissionUrl'
                }, {
                    title: '级别类型',
                    key: 'resourceType'
                }, {
                    title: '操作',
                    key: 'operate',
                    width: 150,
                    align: 'center',
                    render: (h, params) => {

                        if (params.row.status == 0) {
                            return h('div', [
                                h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px',
                                        backgroundColor: '#1fa2fd',
                                        borderColor: '#1fa2fd'
                                    },
                                    on: {
                                        click: () => {
                                            this.$refs.changePermission.beforeLoad(params.row.level);
                                            this.$refs.changePermission.getSysResourceById(params.row.sysResourceid)
                                        }
                                    }
                                }, '修改')
                            ])
                        }

                        return h('div', [
                            h('Button', {
                                props: {
                                    type: 'primary',
                                    size: 'small'
                                },
                                style: {
                                    marginRight: '5px',
                                    backgroundColor: '#1fa2fd',
                                    borderColor: '#1fa2fd'
                                },
                                on: {
                                    click: () => {
                                        this.$refs.changePermission.beforeLoad(params.row.level);
                                        this.$refs.changePermission.getSysResourceById(params.row.sysResourceid)
                                    }
                                }
                            }, '修改'),
                            h('Button', {
                                props: {
                                    type: 'error',
                                    size: 'small'
                                },
                                style: {

                                    backgroundColor: '#ea0b00',
                                    borderColor: '#ea0b00'
                                },
                                on: {
                                    click: () => {
                                        let v = [];
                                        v.push(params.row);
                                        this.deleteBox(v);
                                    }
                                }
                            }, '删除')
                        ]);
                    }

                }

            ],
        }
    },
    created() {
        this.roleList();
    },
    methods: {
        selectData: function(selection) {
            console.log(selection)
            let canDeletes = selection.filter((item) => {
                return item.status == 1;
            });
            this.isDeletes = canDeletes.length == 0;
            this.deleteJurisdictionParams = canDeletes;
        },

        deleteBox(params) {
            console.log(params);
            let sysResourceNames = params.map((item, index) => {
                return item.sysResourceName;
            });
            let sysResourceIds = params.map((item, index) => {
                return item.sysResourceid;
            });
            this.$Modal.confirm({
                title: '删除权限',
                content: `你确定要删除` + sysResourceNames.join(","),
                onOk: () => {
                    console.log(sysResourceIds.join(","))
                    this.deleteMaterialData(sysResourceIds.join(","));
                }
            })
        },

        deleteMaterialData(Resourceid) {
            const url = "/user/deleteSysResource.json?sysResourceId=" + Resourceid;
            console.log(Resourceid)
            this.$Http.get(url, {}).then(res => {
                console.log(res)
                if (res.body.status == 0) {
                    // this.errorMsg = res.body.data;
                    // this.modalLogin=true;
                    //this.roleList();
                    this.$Message.info("删除成功");
                }

            })
        },

        deleteAll: function() {
            this.deleteBox(this.deleteJurisdictionParams);
        },
        toLogin: function() {
            this.$router.push({
                "name": 'login'
            })
        },


        trigger: function() {
            this.isShow = !this.isShow;
        },


        getFilterData() {
            this.loading = true;
            // this.pageSize = 10;
            // this.pageNo = 1;
            this.roleList();
        },


        show(index) {
            this.$Modal.info({
                title: 'User Info',
                content: `Name：${this.data1[index].name}<br>Age：${this.data1[index].age}<br>Address：${this.data1[index].address}`
            })
        },
        remove(index) {
            this.data1.splice(index, 1);
        },
        roleList() {

            const url = "/user/getSysResourceList.json?resourceNameLike=" + encodeURIComponent(this.value) + "&pageSize=10&pageNo=" + this.pageNo + "&status=" + this.roleStatus;
            this.data1 = [];
            this.$Http.get(url, {}).then(res => {
                this.loading = false;
                console.log(res.body);
                // if (res.body.status = 200) {
                //     this.$router.push({name: 'login'});
                //     return;
                // }
                this.permissionArr = res.body.data.result;
                let permissionList = [];
                this.pageSize = res.body.data.totalCount;
                for (var key in this.permissionArr) {
                    permissionList = {
                        'sysResourceName': this.permissionArr[key].sysResourceName,
                        'permissionUrl': this.permissionArr[key].permissionUrl,
                        'level': this.permissionArr[key].level,
                        'parentUrl': this.permissionArr[key].parentUrl == "" ? "／" : this.permissionArr[key].parentUrl,
                        'resourceType': this.permissionArr[key].resourceType == 1 ? '页面' : "功能",
                        'sysResourceid': this.permissionArr[key].sysResourceid,
                        "key": key,
                        "status": this.permissionArr[key].status,
                        'statusDesc': this.permissionArr[key].status == 0 ? "删除" : "启用"
                    }
                    this.data1.push(permissionList);
                }
                console.log(this.data1)
            })
        },
        thisRouter() {
            this.$refs.setRole.beforeLoad();
        },
        clickPage(data) {
            this.loading = true;
            console.log(data);
            this.pageNo = data;
            this.data1 = [];
            this.roleList();
        },
        search() {
            // alert(this.value)
            this.data1 = [];
            this.roleList()
        },
        ok() {
            var Resourceid = '';
            if (this.delBol == false) {
                Resourceid = this.dataSysUserId;
            } else {
                Resourceid = this.selectTabAllsysResourceid
            }
            const url = "/user/deleteSysResource.json?sysResourceId=" + Resourceid;
            console.log(url);
            this.$Http.get(url, {}).then(res => {
                console.log(res);
                if (res.body.status == 0) {
                    this.$Message.info(res.body.msg);
                    for (var i = 0; i < this.deleAllArr.length; i++) {
                        this.data1.splice(this.deleAllArr[i], 1);
                    }

                } else if (res.body.status == 200) {
                    this.$router.push({
                        name: 'login'
                    });
                    this.$Message.info(res.body.msg);
                } else {
                    this.$Message.info(res.body.msg);
                }


            })
        },
        cancel() {
            this.$Message.info('已取消删除');
        },
        selectTabdelAllid(data) {
            // console.log(data)
            this.delBol == true;
            let str = "";
            for (var i = 0; i < data.length; i++) {
                if (i == data.length - 1) {
                    str += data[i].sysResourceid
                } else {

                    str += data[i].sysResourceid + ","
                }
            }
            console.log(str)
            this.selectTabAllsysResourceid = str;

        },
        delAllUserId() {
            this.modal1 = true;

        },
    }
}

</script>



