<template>
  <div class="form-wrap">
    <Modal
      v-model="modal"
      title="修改权限功能" style="{width:100%}" class-name="modal-box">
      <div class="fu">
        <div class="RadioGroup-wrap">
          <span class="label-left">级别类型</span>
          <RadioGroup @on-change='clickRadio' v-for='(item,index) in resourceLevel' :key="index" v-model='level'>
            <Radio :label='item.id'>{{item.name}}</Radio>
          </RadioGroup>
        </div>
        <ul class="content-page">
          <li v-show="ishow2">
            <div class="content-input">
              <span class="label-left">所属一级分类：</span>
              <Select v-model="parentId" style="width:200px">
                <Option v-for="(item,index) in resourceListFirst" :value="item.id" :key="item.id">{{ item.name }}</Option>
              </Select>
            </div>
          </li>
          <li v-show="ishow3">
            <div class="content-input">
              <span class="label-left">所属二级分类：</span>
              <Select v-model="parentId" style="width:200px">
                <OptionGroup :label="item.name" v-for="(item,index) in resourceListSecond" :key="index">
                  <Option v-for="itemChild in item.childList" :value="itemChild.id" :key="itemChild.id">{{
                    itemChild.name }}
                  </Option>
                </OptionGroup>
              </Select>
            </div>

          </li>
          <div class="content-input">
            <span class="label-left">权限功能名称：</span>
            <Input @on-blur='userDataNum' v-model="sysResourceName" placeholder="请输入权限功能名称"
                   style="width: 300px"></Input>
            <span :class="{'label-right-active':isAtive}" class="label-right">{{soltNameLen}}</span>
          </div>
          <div class="content-input">
            <span class="label-left">URL：</span>
            <Input @on-blur='urlBlur' v-model="permissionUrl" placeholder="请输入地址" style="width: 300px"></Input>
            <span :class="{'label-right-active':isUrlAtive}" class="label-right">{{soltNameLenUrl}}</span>
          </div>
        </ul>


      </div>
      <div slot="footer">
        <Button @click='modal=false' style="margin-right:20px;">取消</Button>
        <Button class="btnColor" type="primary" @click="updateSysResource">修改并启用</Button>
      </div>
    </Modal>

  </div>
</template>
<style scoped>
  .RadioGroup-wrap {
    margin-bottom: 20px;

  }

  .content-page {
    margin-bottom: 20px;
  }

  .label-left {
    display: inline-block;
    width: 100px;
  }

  .content-input {
    padding: 10px 0px;
  }

  .btn-wrap-set {
    position: fixed;
    bottom: 50px;
  }

  .label-right-active {
    color: red !important;
  }
</style>
<script>
  export default {
    data() {
      return {
        modal: false,
        level: 1,
        sysResourceName: '',
        parentId: "",
        btndRadio: 1,
        soltNameLen: '最多可输入100个字符',
        soltNameLenUrl: '请输入URL',
        permissionUrl: '',
        isAtive: false,
        resourceLevel: [],
        ishow1: true,
        ishow2: false,
        ishow3: false,
        isUrlAtive: false,
        resourceListFirst: [],
        resourceListSecond: [],
        sysResourceId: '',

      }
    },
    created() {
      this.getAdResourceLevel();
      this.SelectGroupFirst();
      this.SelectGroupSecond();
    },

    mounted() {
      // this.getAdResourceLevel();
      // this.SelectGroupFirst();
      // this.SelectGroupSecond();

    },

    methods: {
      beforeLoad(level) {
        this.btndRadio = "";
        this.parentId = "";
        ;
        this.sysResourceName = "";
        ;
        this.permissionUrl = "";
        this.soltNameLen = "最多可输入100个字符";
        this.isAtive = false;
        this.soltNameLenUrl = "请输入URL"
        this.isUrlAtive = false;
        this.modal = true;

        this.clickRadio(level);

      },
      cancel() {
        this.soltNameLen = "最多可输入100个字符";
        this.isAtive = false;
        this.soltNameLenUrl = "请输入URL"
        this.isUrlAtive = false;
        this.modal = false;
      },
      userDataNum() {
        //姓名位数不在0-100间
        if (this.sysResourceName) {
          var reg = /^[\s\S]{1,100}$/;
          var r = this.sysResourceName.match(reg);
          if (r == null) {
            this.soltNameLen = "长度超过一百个字符"
            this.isAtive = true;
          }
        } else {
          this.soltNameLen = "权限功能名称不能为空"
          this.isAtive = true;
        }
      },
      onFocusName() {
        this.soltNameLen = "最多可输入100个字符";
        this.isAtive = false;

      },
      getAdResourceLevel() {
        const url = "/common/getAdResourceLevelMap.json?level=2"
        this.$Http.post(url, {}).then(res => {

          console.log(res)


          this.resourceLevel = res.body.data.list;

        })
      },
      urlBlur() {
        if (this.permissionUrl == "") {
          this.soltNameLenUrl = "URL不能为空"
          this.isUrlAtive = true;
        } else {
          this.soltNameLenUrl = "请输入URL"
          this.isUrlAtive = false;
        }
      },
      clickRadio(data) {
        console.log(data);
        this.level = data;
        if (data === 1) {
          this.ishow1 = true;
          this.ishow2 = false;
          this.ishow3 = false;
        } else if (data === 2) {
          this.ishow1 = false;
          this.ishow2 = true;
          this.ishow3 = false;
        } else if (data === 3) {
          this.ishow1 = false;
          this.ishow2 = false;
          this.ishow3 = true;
        }
      },
      SelectGroupFirst() {
        const url = "/common/getResourceListByLevel.json?level=1&parentId"
        this.$Http.get(url, {}).then(res => {
          console.log(res.body.data.resourceList);
          this.resourceListFirst = res.body.data.resourceList;
          // console.log(tablList);
        })
      },
      SelectGroupSecond() {
        const url = "/common/getResourceList.json?level=2&nameLike="
        this.$Http.get(url, {}).then(res => {
          console.log(res.body.data.resourceList);
          this.resourceListSecond = res.body.data.resourceList;
          // console.log(tablList);
        })
      },
      updateSysResource() {
        if (this.permissionUrl == "") {
          this.isUrlAtive = true;

          return false;
        }
        const url = `/user/updateSysResource.json?level=${this.level}&permissionUrl=${this.permissionUrl}&sysResourceName=${encodeURIComponent(this.sysResourceName)}&parentId=${this.parentId}&sysResourceId=${this.sysResourceId}`;
        this.$Http.get(url, {}).then(res => {
          console.log(res)
          if (res.body.status === 0) {
            this.modal = false;
            this.$emit("filterParent");
          }

        })
      },
      getSysResourceById(sysResourceId) {
        this.sysResourceId = sysResourceId;
        const url = `/user/getSysResourceById.json?sysResourceId=${sysResourceId}`
        this.$Http.get(url, {}).then(res => {
          if (res.body.status === 0) {
            console.log(res);
            this.level = res.body.data.level;
            this.parentId = res.body.data.parentId;
            this.sysResourceName = res.body.data.sysResourceName;
            this.permissionUrl = res.body.data.permissionUrl;
          }


        })
      },
      urlBlur() {
        if (this.permissionUrl == "") {
          this.soltNameLenUrl = "URL不能为空"
          this.isUrlAtive = true;
        } else {
          this.soltNameLenUrl = "请输入URL"
          this.isUrlAtive = false;
        }
      },

    }
  }
</script>



